import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useState } from 'react';
import css from './UserLogo.module.css';
import { selectUserInfo } from '../../redux/user/selectors';
import { fetchUser } from '../../redux/user/operations';
import UserLogoModal from 'components/UserLogoModal/UserLogoModal';

const UserLogo = () => {
  const dispatch = useDispatch();
  const [open, setOpen] = useState(false);

  //  useEffect   
  useEffect(() => {
    dispatch(fetchUser());
  }, [dispatch]);

  const userInfo = useSelector(selectUserInfo);

  //   userInfo
  if (!userInfo) {
    return <div>...</div>; //    ,     
  }

  const data = userInfo.data || {}; //   ,   

  let avatarSrc = '';
  let avatarText = '';
  let displayName = '';

  //      
  if (data.avatarUrl) {
    avatarSrc = data.avatarUrl;
    if (data.name) {
      displayName = data.name;
    } else {
      displayName = data.email;
    }
  } else if (data.name) {
    avatarText = data.name.charAt(0).toUpperCase(); //   
    displayName = data.name; //  
  } else if (data.email) {
    avatarText = data.email.charAt(0).toUpperCase(); //   email
    displayName = data.email; // Email 
  }

  const openDropdown = e => {
    e?.stopPropagation();
    setOpen(prev => !prev);
  };

  return (
    <div className={css.main}>
      <button
        type="button"
        className={css.btn}
        onClick={openDropdown}
        onMouseDown={e => e.stopPropagation()}
      >
        <div className={css.wrapper}>
          <div className={css.infoWrapper}>
            {displayName && (
              <span className={css.avatarText}>{displayName}</span>
            )}
            <div className={css.avatarText}>
              {avatarSrc ? (
                <img src={avatarSrc} alt="avatar" className={css.avatarImg} />
              ) : (
                <span className={css.spanName}>{avatarText}</span>
              )}
            </div>
          </div>

          <div className={css.headerActions}>
            <div>
              <svg className={css.icon}>
                <use href="/the-DOMinators-frontend-project/welcomeIcons.svg#tick" />
              </svg>
            </div>
          </div>
        </div>
      </button>

      {open && (
        <div className={css.modal}>
          <UserLogoModal onClose={openDropdown} isOpen={open} />
        </div>
      )}
    </div>
  );
};

export default UserLogo;
